@font-face {
  font-family: "Inter";
  src: url("../fonts/Inter-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Inter";
  src: url("../fonts/Inter-Medium.woff2") format("woff2");
  font-weight: 500;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Inter";
  src: url("../fonts/Inter-SemiBold.woff2") format("woff2");
  font-weight: 600;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Inter";
  src: url("../fonts/Inter-Bold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap; }

* {
  box-sizing: border-box; }

html {
  height: 100%; }

body {
  font-family: "Inter", "Arial", sans-serif;
  font-size: 14px;
  line-height: 1.4;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  margin: 0;
  width: 100%;
  min-width: 320px;
  min-height: 100%;
  color: #2C2C2C; }

.header,
.footer {
  flex-shrink: 0; }

main {
  flex-grow: 1; }
  @media (max-width: 767px) {
    main {
      margin-top: -70px; } }

a {
  color: inherit;
  text-decoration: none;
  cursor: pointer; }

img {
  display: block;
  max-width: 100%;
  height: auto; }

button,
input {
  cursor: pointer; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  overflow: hidden; }

.scroll-lock {
  min-height: 100vh;
  overflow: hidden; }

.container {
  width: 100%;
  max-width: 1440px;
  padding: 50px;
  margin: 0 auto; }

.input {
  display: flex;
  flex-direction: column;
  width: 260px;
  margin: 0 0 40px; }
  .input input,
  .input select {
    font-size: 13px;
    line-height: 16px;
    width: 100%;
    height: 37px;
    padding: 0 12px;
    color: #2C2C2C;
    border: 1px solid #E0E0E0;
    border-radius: 4px;
    outline: none; }
    .input input::placeholder,
    .input select::placeholder {
      color: rgba(44, 44, 44, 0.5); }
  .input label {
    font-size: 13px;
    line-height: 16px;
    font-weight: 500;
    margin-bottom: 7px; }
  .input select {
    color: rgba(44, 44, 44, 0.5);
    -webkit-appearance: none;
    background-image: url("../img/select-down.svg");
    background-repeat: no-repeat;
    background-position: 230px center;
    cursor: pointer; }

input:invalid {
  color: #FF3E1D;
  background-color: rgba(255, 62, 29, 0.06);
  border: 1px solid #FF3E1D; }

input:disabled {
  color: rgba(44, 44, 44, 0.5);
  background-color: rgba(224, 224, 224, 0.3); }

.input-serch {
  position: relative; }
  .input-serch label {
    content: "";
    position: absolute;
    width: 15px;
    height: 15px;
    margin: 0; }
    .input-serch label svg {
      color: rgba(44, 44, 44, 0.5); }

.input-serch--left input {
  padding-left: 37px; }

.input-serch--left label {
  left: 13px;
  top: 12px; }

.input-serch--right {
  position: relative; }
  .input-serch--right label {
    right: 10px;
    top: 12px; }

.input--web {
  position: relative;
  border-radius: 4px;
  overflow: hidden; }
  .input--web input {
    padding-left: 70px; }
  .input--web label {
    position: absolute;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 60px;
    height: 100%;
    margin: 0;
    background-color: rgba(224, 224, 224, 0.3);
    color: rgba(44, 44, 44, 0.5); }

.textarea {
  display: flex;
  flex-direction: column;
  width: 260px;
  margin: 0 0 40px; }
  .textarea textarea {
    font-size: 13px;
    line-height: 16px;
    width: 100%;
    min-height: 97px;
    padding: 10px 16px;
    color: #2C2C2C;
    border: 1px solid #E0E0E0;
    border-radius: 4px;
    outline: none; }
    .textarea textarea::placeholder {
      color: rgba(44, 44, 44, 0.5); }
  .textarea textarea:invalid {
    color: #FF3E1D;
    background-color: rgba(255, 62, 29, 0.06);
    border: 1px solid #FF3E1D; }
  .textarea label {
    margin-bottom: 8px; }

.checkbox label {
  position: relative;
  font-weight: 500;
  font-size: 13px;
  line-height: 16px;
  color: #2C2C2C;
  padding-left: 24px;
  cursor: pointer; }
  .checkbox label::before {
    content: "";
    position: absolute;
    left: 0;
    top: 50%;
    transform: translate(0, -50%);
    width: 16px;
    height: 16px;
    border: 1px solid #E0E0E0;
    border-radius: 4px; }
  .checkbox label::after {
    content: "";
    position: absolute;
    left: 4px;
    top: 50%;
    transform: translate(0, -50%);
    width: 11px;
    height: 8px;
    background-image: url(../img/checkbox.svg);
    background-size: contain;
    background-repeat: no-repeat; }

.checkbox input {
  display: none; }

.checkbox input + label::after {
  display: none; }

.checkbox input:checked + label::after {
  display: block; }

.checkbox input:checked + label::before {
  background-color: #18A0FB;
  border-color: #18A0FB; }

.checkbox input:disabled + label::before {
  background-color: rgba(224, 224, 224, 0.3); }

.checkbox--circle label::before {
  border-radius: 50%; }

.checkbox--circle label::after {
  left: 5px;
  width: 8px;
  height: 8px;
  background-image: none;
  border-radius: 50%;
  background-color: white; }

.switch {
  position: relative;
  display: inline-block;
  width: 52px;
  height: 32px; }
  .switch input {
    display: none; }
  .switch__title {
    font-weight: 500;
    font-size: 13px;
    line-height: 32px;
    padding-left: 62px;
    cursor: pointer; }
  .switch__slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: white;
    border: 1px solid #E0E0E0;
    transition: .4s; }
  .switch__slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 2px;
    bottom: 2px;
    background-color: #E0E0E0;
    transition: .4s; }
  .switch input:checked + .switch__slider {
    background-color: #00D200;
    border-color: #00D200; }
  .switch input:checked + .switch__slider:before {
    background-color: white;
    transform: translateX(20px); }
  .switch .switch__slider.round {
    border-radius: 19px; }
  .switch .switch__slider.round:before {
    border-radius: 50%; }
  .switch input:disabled + .switch__slider {
    background-color: #E0E0E0;
    border-color: #E0E0E0; }
  .switch input:disabled + .switch__slider:before {
    background-color: white; }

.switch--small {
  height: 24px; }
  .switch--small .switch__slider:before {
    content: "";
    width: 20px;
    height: 20px;
    bottom: 1px; }
  .switch--small input:checked + .switch__slider:before {
    transform: translateX(26px); }
